ul, ol, li { list-style:none; margin:0; padding:0; }

ul.myMenu {}
ul.myMenu > li {
    font-size: 20px;
    display:inline-block; 
    width:200px; 
    padding:5px 10px; 
    background:white;
    text-align:center; 
    position: relative; 
}
ul.myMenu > li ul.submenu { display:none; position: absolute; top:30px; }
ul.myMenu > li:hover { cursor: pointer; }
ul.myMenu > li:hover ul.submenu { display:block; }
ul.myMenu > li ul.submenu > li { display:inline-block; width:95px; padding:5px 10px; background:#fff; text-align:center; float: left; font-size: 15px;}
ul.myMenu > li ul.submenu > li:hover { background:#f1f1f1; cursor: pointer; }

